import React from 'react'
import { withPrefix } from 'gatsby-link'
import Layout from '../components/layout'
import ScrollSection from '../components/ScrollSection'
import PreviousButton from '../components/PreviousButton'
import NextButton from '../components/NextButton'

class IndexPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      inViewport: '',
    }
  }

  onEnterViewport = (id) => {
    history.replaceState(undefined, undefined, `#${id}`)
    this.setState({
      inViewport: id,
    })
  }

  render() {
    const {
      inViewport,
    } = this.state

    return (
      <Layout bodyClass={inViewport}>
        <div className="content">
          <div className="ui text container list">
            <ScrollSection onEnter={this.onEnterViewport} id="story.fairytales.rapunzel">
              <h3>Aranyhaj</h3>
              <div>
                <p>Aranyhaj avagy Galambbegy (avagy Rapuncel) történetén keresztül jobban megismerhetjük Hófehérke és a gonosz mostoha kapcsolatát. Aranyhaj - Hamupipőkéhez hasonlóan - valójában mostohagyermek. Hogy mit is jelent a mostoha fogalma, az itt is relatív. Hófehérke a Grimm 1.0-ás verziójában (1812-es kiadás) még a saját anyja elől menekül, aki el akarja pusztítani. Állítólag a Grimmek szépítették meg a történetet, mivel a germanisztika tárgyköréhez a dicső Német múlt is hozzátartozott, így elfogadhatatlanná vált a gyermekgyilkos anya karaktere, ezért inkább hagyták meghalni az eredetit, és egy mostohára bízták a gonosz szerepét. Szerintem ez nem hogy ártott a mesének, de sokat dobott rajta. A karakterek ugyanis nyilvánvalóan szimbolikusak, ezt próbálom mindvégig magyarázni. Egy olyan sejtésem is van, hogy az első mese még kiforratlan volt, nem találtak elegendő forrást, így maradt az, amire valaki emlékezett. Az illető valószínűleg szívesebben evett tüdőt májjal, mint szívet, így ez a baki is a mesében maradt.</p>

                <p>Aranyhaj történetében az anya (mostoha) egy toronyba zárja engedetlen lányát. A torony börtönként tartja fogva a lányt, nincs kiút, így a lány rá van szorulva a mostoha segítségére. A mostoha úgy tartja a kapcsolatot a lánnyal, hogy annak hosszú haján kapaszkodik fel. Mikor kiderül, hogy a lány felnőtt életet szeretne élni, a mostoha megcsonkítja, levágja a lány szép haját, mondván, ha az övé nem lehet, ne legyen senkié.</p>

                <p><img src={withPrefix('images/fairytales/3e8343cd3c2ff5b1ea9920de29ca62cd.jpg')} className="ui fluid image" /></p>

                <p>A kapcsolat a két mese között az a pont lehet, amikor Hófehérkét a mostohája fésűvel, majd fűzővel próbálja meg eltenni láb alól első két próbálkozása során.</p>
                <p style={{ columnCount: 2, columnGap: '10px' }}>
                  <img src={withPrefix('images/snowwhite/witch/36c8dba30d772552a1eecaab1943a3d2.jpg')} className="ui fluid image" />
                  <img src={withPrefix('images/fairytales/7475ed243b80dc09733efd27341b8bf7.jpg')} className="ui fluid image" />
                </p>

                <p>A meséket összekötő kapocs a leánygyermek és anyja közt fennálló beteges viszony. Míg Hófehérkét anyja elűzi otthonról, addig Aranyhajat pont hogy fogva tartja a sajátja, de ez ne tévesszen meg minket. Hófehérke mostohája utána megy a szökött lánynak, Aranyhajé pedig magára hagyja a lányt, egyedül a börtönében. Ugyanazon viszály különböző megjelenési formái ezek, nevezetesen az anya lánygyermekét csak gyermeki, és mint olyan, csak alárendelt szerepben képes elfogadni. Valójában fél a kibontakozó nőtől, így megpróbálja megfékezni azt, ha kell megnyomorítja, mérgezi, elfolytja annak kifejlődését. Ez teszi mostohává a mostohát, hogy akár édes gyermekével is mostohán bánik, nem a konkrét vérségi viszony számít itt, hanem a szándék, a mód.</p>

                <p>Az anya gondoskodóból mostohává váló alakulásának okát egy példával próbálom meg bevezetni. Vegyük példaként a szépen kisült kenyeret. A megkelt tésztát kemencébe kell tenni, és addig sütni, amíg épp jó nem lesz. Nem kevésbé, nem tovább. Ha a kenyér nem sül meg, nyers marad, ha túl sokáig sül, akkor feketére ég. Egy tehetséges anya tudja, meddig kell sütni a kenyeret, mert ért az árulkodó jelekből. Aki viszont nem ért ebből, az más eligazodási pontot keres, például az időt méri. Sok minden működhet, és amíg működik, addig minden rendben. Képzeljük el, hogy felnő egy gyermek, akinek a szülei már nem az árulkodó jelek kiismerését tanították meg csemetéjükkel, pusztán azt, hogy mennyi ideig kell várni, míg a kenyér kisül. A leány felnő, és nagy, tüzes kemencét építenek. De valahogy ez sokkal forróbb, mint ami a szülői házban volt, így azonos időegység alatt a kenyér mindig odaég. Az ő gyermekei égett kenyeret esznek, és megtanulják, hogy a kenyeret az előtt kell kivenni a kemencéből, mielőtt az még megsülne. És mikor felnőnek, akkor ők sokkal gondoskodóbbak lesznek gyermekeikkel, nehogy azoknak is olyan szörnyű élete legyen, mint nekik, hogy az égett kenyeret kell enniük, így mindig hamarabb veszik ki a kenyeret, és gyermekeik nem értik, hogy az anyu miért ad nekik mindig nyers kenyeret. Erre fel ők is fogadalmat tesznek, hogy majd ők hogyan fogják sütni a kenyeret, ha felnőnek. És így tovább.</p>

                <p>Az életet számtalan módon el lehet b@$#ni. A szülők szeretnek okosabbnak tűnni gyermeküknél, és kioktatni őket, hogy ne szúrják el a dolgokat, ahogyan azt ők elszúrták. Ez a magatartás sok okból nem túl segítőkész. A gyerek eleve látja, mitől szenved a szülő, nehéz lenne átverni. A gyerek ilyenkor már meghozza a következtetést maga is. Különben miért hallgatna olyanra, aki maga is elismeri, hogy valamit nem jól csinált, és most oktatást tart pont abból a témából amit nem jól csinál, vagy akár totálisan kudarcos... Könnyű az mondani, hogy hogyan ne, de nehéz jó példát mutatni, hogy hogyan igen. Az előző generációk életpéldája nagyon is meghatározó, komoly komplexusok ragadhatnak át a szülőkről gyermekeikre, és aztán tovább, azok gyermekeire. Olyan ez, mint egyfajta átok, valahogy rajta ül egy családon, és tagjai nem menekülhetnek.</p>

                <p>A mesebeli Hófehérke és Aranyhaj anyja nem mutatnak jó példát, sőt, az elkövethető hibák legsúlyosabb eseteit mutatják be, ők a tipikus pszichopata anyák, akik terrorizálják lányukat. Említettem a kenyér kisütésének példáját, hogy se túl korán, se túl későn. Egy kenyér sokkal egyszerűbb dolog, mint egy leánygyermek, de még azt is el lehet cseszni. Hófehérke mostohája idejekorán engedi szabadon a lányt, aki így a felkészületlenül esik a vadász áldozatául, Aranyhaj mostohája meg egyáltalán nem akarja elengedni, inkább magához láncolja szerencsétlen gyermekét. Gondolok itt olyan általános életpéldákra, amikor azt hallani a szülők elleni panaszként, hogy nem engedtek sehova, este nyolcra mindig otthon kellett lennem, ide meg oda nem mehettem, vagy éppen olyanokat, hogy nem fogtak elég komolyan, nem fegyelmeztek, pedig tudták, milyen veszélyes amit csinálok, lám ezért vagyok most ilyen-olyan.</p>

                <p>De vannak súlyosabb esetek is. Aranyhaj mostohája valósággal megmételyezi a lányt, hogy lám, ő az egyetlen, aki igazán szereti, az anya szeretete felülmúlhatatlan, csak mellette lehet biztonságban. </p>

                <p><img src={withPrefix('images/fairytales/9bf6eec12c0887c119cd9b5be6d0c0c3.jpg')} className="ui fluid image" /></p>
              </div>
            </ScrollSection>
          </div>
        </div>

      </Layout>
    )
  }
}

export default IndexPage
